import { h, cloneElement, render, hydrate } from 'preact';

/* Preact „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„Åã„Çâ CustomElement „ÇíÁîü„ÇÑ„ÅôÂá¶ÁêÜ„ÅÆÊú¨‰Ωì */
const makeCustomElement = (Component, propNames, options) => {
  function PreactElement () {
    /* „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÇíÂÖ•„Çå„Çã CustomElement „ÅÆ„Ç§„É≥„Çπ„Çø„É≥„Çπ„ÇíÁîü„ÇÑ„Åô */
    const instance = Reflect.construct(HTMLElement, [], PreactElement);
    instance._Component = Component;
    /* options.shadow „ÅåÊúâÂäπ„Å™„Çâ attachShadow „Åô„Çã */
    instance._root = options && options.shadow ? (
      instance.attachShadow({ mode: options.mode || 'open' })
    ) : (
      instance
    );
    if (options.adoptedStyleSheets) {
      instance._root.adoptedStyleSheets = options.adoptedStyleSheets;
    }
    return instance;
  }
  PreactElement.prototype = Object.create(HTMLElement.prototype);
  PreactElement.prototype.constructor = PreactElement;

  /* „Åì„ÅÑ„Å§„Çâ„Åå„Çè„Åã„Çâ„Å™„ÅÑÂ∫¶È´ò„ÅÑ */
  PreactElement.prototype.connectedCallback = connectedCallback;
  PreactElement.prototype.attributeChangedCallback = attributeChangedCallback;
  PreactElement.prototype.disconnectedCallback = disconnectedCallback;

  /* propNames ÂºïÊï∞„Åã„Çâ („ÅÇ„Çã„ÅÑ„ÅØ .propTypes „Åã„Çâ prop „ÅÆ‰∏ÄË¶ß„ÇíÊãµ„Åà„Çã) */
  propNames = propNames || Component.observedAttributes || Object.keys(Component.propTypes || {});
  PreactElement.observedAttributes = propNames;

  /* DOM props „Å® Preact props „ÅÆÂêåÊúü */
  propNames.forEach((name) => {
    Object.defineProperty(PreactElement.prototype, name, {
      get() {
        return this._vdom.props[name];
      },
      set(v) {
        /* _vdom „ÅØ connectedCallback „Åß„Çª„ÉÉ„Éà„Åï„Çå„Å¶„ÇãÂÄ§„Å£„ÅΩ„ÅÑ
         * „Å§„Åæ„ÇäÂàùÂõû„ÅØ„Åì„ÇåÂëº„Å∞„Çå„Å™„ÅÑÔºü */
        if (this._vdom) {
          /* Atrribute „ÅåÂ§â„Çè„Å£„ÅüÊôÇ„Å®Âêå„ÅòÂá¶ÁêÜ„Çí„Åô„Çã */
          this.attributeChangedCallback(name, null, v);
        } else {
          /* ÂàùÂõû„É¨„É≥„ÉÄ„ÅÆ„Åü„ÇÅ„ÅÆ props „Çí„Çª„ÉÉ„Éà„Åô„Çã„ÄÇ
           * ‰∏ÄÂ∫¶ VDom „Åå‰Ωú„Çâ„Çå„Åü„Çâ„ÄÅ‰ª•Èôç„ÅØ attributeChangedCallback „Åå‰ªï‰∫ã„Çí„Åô„Çã„Çâ„Åó„ÅÑ */
          /* „Åì„Çå attributes „ÅßÊ∏°„Åï„Çå„ÅüÊôÇ„ÅØ„Å©„ÅÜ„Åô„Çã„Çì„Åò„ÇÉ„Çç */
          if (!this._initialProps) this._initialProps = {};
          this._initialProps[name] = v;
          this.connectedCallback();
        }

        /* prop „Çí Attribute „Å´„ÇÇ reflect „Åô„Çã (primitive „Å™ÂÄ§„ÅÆÂ†¥Âêà„ÅÆ„Åø) */
        const type = typeof v;
        if (
          v == null ||
          type === 'string' ||
          type === 'boolean' ||
          type === 'number'
        ) {
          this.setAttribute(name, v);
        }
      },
    });
  });

  return PreactElement;
};

/* CustomElement „Çí (makeCustomElement „Åß) Áîü„ÇÑ„Åó„Å¶„ÄÅÁôªÈå≤„Åô„Çã */
export function register(Component, tagName, propNames, options) {
  const element = makeCustomElement(Component, propNames, options);
  const tag = tagName || Component.tagName || Component.displayName || Component.name;
  return customElements.define(tag, element);
}

/* Ê∏°„Å£„Å¶„Åç„Åü context „Çí ".getChildContext" „ÅßÂèñ„Çå„Çã„Çà„ÅÜ„Å´„Åô„Çã Preact Component „Å£„ÅΩ„ÅÑ„ÄÇ
 * props „ÅØÂ≠êË¶ÅÁ¥†„ÅÆ Component „Å´„Åù„ÅÆ„Åæ„ÅæÂßîË≠≤„Åó„Å¶„ÅÑ„Çã */
function ContextProvider ({ context, children, ...props }) {
  this.getChildContext = () => context;
  /* Â≠ê„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„Çí„ÄÅÂºïÊï∞„Çí {...props} „Å®„Åó„Å¶ÂÜç„É¨„É≥„ÉÄ„Åô„Çã */
  return cloneElement(children, props);
}

/* ÂàùÂõû„É¨„É≥„ÉÄ„É™„É≥„Ç∞„ÅßÂëº„Å∞„Çå„ÇãÈñ¢Êï∞ */
function connectedCallback() {
  /* Áã¨Ëá™„Ç§„Éô„É≥„Éà„ÇíÁô∫Â∞Ñ„Åó„Å¶„Åø„Å¶„ÄÅÊãæ„Å£„Å¶„Åè„Çå„Çã„ÅÆ„ÇíÂæÖ„Å§„Å£„ÅΩ„ÅÑ„ÄÇ
   * „Åì„ÅÆ„Ç§„Éô„É≥„Éà„ÇíÊãæ„ÅÜ„ÅÆ„ÅØ "Slot" „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà */
  const event = new CustomEvent('_preact', {
    detail: {},
    bubbles: true,
    cancelable: true,
  });
  this.dispatchEvent(event);
  /* Slot ÂÅ¥„Åå„Çª„ÉÉ„Éà„Åó„ÅüÂÄ§„Çí„Åì„Åì„ÅßÂõûÂèé„Åó„Å¶„Çã„Å£„ÅΩ„ÅÑ */
  const context = event.detail.context;

  /* Slot „Åã„ÇâÊ∏°„Å£„Å¶„Åç„Åü context „Åã„Çâ ContextProvider „Çí‰Ωú„Å£„Å¶„ÄÅ
   * „Åù„ÅÆ‰∏ã„Å´ Component („ÅÆ VDom) „ÇíÊèèÁîª„Åô„Çã
   *
   *   <ContextProvider context={context} props={this._initialProps}>
   *     <Component />
   *   </ContextProvider>
   *
   * Component „Å´Áõ¥Êé• prop „ÅåÊ∏°„Åï„Çå„Å¶„Å™„ÅÑ„Åë„Å©„ÄÅContextProvider „Å´ props „ÇíÂßîË≠≤„Åô„ÇãÊ©üÊßã„ÅåÂÖ•„Å£„Å¶„ÅÑ„Çã„ÄÇ
   * props Êõ¥Êñ∞„ÅÆÂá¶ÁêÜ„ÇíÁ∞°Âçò„Å´„Åô„Çã„Åü„ÇÅ„Å´„ÄÅ„É´„Éº„ÉàË¶ÅÁ¥†„Å´ props „ÇíÊåÅ„Åü„Åõ„Åü„ÅÑÁÇ∫„Å†„Å®ÊÄù„Çè„Çå„Çã„ÄÇ
   */
  this._vdom = h(
    ContextProvider,
    { ...this._initialProps, context },
    toVdom(this, this._Component)
  );
  /* "hydrate" attr „Çí„Çª„ÉÉ„Éà„Åó„Å¶„Åä„Åè„Å® hydrate „Åó„Å¶„Åè„Çå„Çã„Å£„ÅΩ„ÅÑ„ÄÇ„Åä„Åù„Çâ„Åè SSR Áî®ÈÄî
   * Readme „Å´Ë™¨Êòé„Åå„Å™„ÅÑ„ÅÆ„Åß„Å°„Çá„Å£„Å®„Çè„Åã„Çâ„Å™„ÅÑ„ÄÇË™∞„Åå "hydrate" „Çí„Çª„ÉÉ„Éà„Åó„Å¶„Åè„Çå„Çã„Çì„Å†„Çç„ÅÜ
   *
   * „Åü„Å®„Åà„Å∞„Å†„Åë„Å©„ÄÅ
   *
   *   <my-hello />
   *
   * „Çí„Åì„Çì„Å™ÊÑü„Åò„Å´ SSR „Åó„Å¶„Åä„Åë„Çã„ÅÆ„Åã„Å™
   *
   *   <my-hello hydrate>
   *     <root>
   *       <span hydrate>Hello!</span>
   *     </root>
   *   </my-hello>
   */
  (this.hasAttribute('hydrate') ? hydrate : render)(this._vdom, this._root);
}

function toCamelCase(str) {
  return str.replace(/-(\w)/g, (_, c) => (c ? c.toUpperCase() : ''));
}

function attributeChangedCallback(name, oldValue, newValue) {
  /* ÂàùÂõû„É¨„É≥„ÉÄ„Åå„Åæ„Å†„Å™„ÇâÁâπ„Å´„ÇÑ„Çã„Åì„Å®„Å™„Åó„ÄÇÊúÄÊñ∞„ÅÆ prop „ÅßÂàùÂõû„É¨„É≥„ÉÄ„Çí„Åô„Çã„Å†„Åë„Å™„ÅÆ„Åß */
  if (!this._vdom) return;
  /* Attribute „ÅÆ„Éá„Éï„Ç©„É´„ÉàÂÄ§„ÅØ null „Å†„Åå„ÄÅPreact ‰∏ñÁïå„Åß„ÅØ„Åã„Çè„Çä„Å´ undefined „Çí‰Ωø„ÅÜ */
  newValue = newValue == null ? undefined : newValue;
  const props = {};
  /* Attribute "my-attr" „Å´ÂØæÂøú„Åô„Çã prop „Åå "myAttr" „Åß„ÅÇ„Çã„Åì„Å®„Çí‰ªÆÂÆö„Åó„Å¶„ÅÑ„Çã„Å£„ÅΩ„ÅÑ */
  props[name] = newValue;
  props[toCamelCase(name)] = newValue;
  /* UI „ÇíÊõ¥Êñ∞ (ÁèæÂú®„ÅÆ UI „Å´„ÄÅprops „Çí‰∏äÊõ∏„Åç„Åó„Å¶ÂÜç„É¨„É≥„ÉÄ„Çí„Åã„Åë„Çã) */
  this._vdom = cloneElement(this._vdom, props);
  render(this._vdom, this._root);
}

/* VDom „ÇíÊ∂à„Åó„Å¶„ÄÅ UI „ÇíÁâá‰ªò„Åë„Çã */
function disconnectedCallback() {
  this._vdom = null;
  render(null, this._root);
}

/* ------------ „Åì„Åì„Åæ„ÅßË™≠„Çì„Å† */

/**
 * Pass an event listener to each `<slot>` that "forwards" the current
 * context value to the rendered child. The child will trigger a custom
 * event, where will add the context value to. Because events work
 * synchronously, the child can immediately pull of the value right
 * after having fired the event.
 */
function Slot (props, context /* „Åì„ÅÑ„Å§„ÅØ VNode */) {
  const ref = (r) => {
    if (!r) {
      this.ref.removeEventListener('_preact', this._listener);
    } else {
      this.ref = r;
      if (!this._listener) {
        this._listener = (event) => {
          event.stopPropagation();
          event.detail.context = context;
        };
        r.addEventListener('_preact', this._listener);
      }
    }
  };
  return h('slot', { ...props, ref });
}

/* „Äåelement „ÅÆ‰ΩçÁΩÆ„Å´ nodeName (Component „Åæ„Åü„ÅØ„Çø„Ç∞Âêç) „ÇíÊèèÁîª„Åó„ÅüÊôÇ„ÅÆ VDom„Äç„ÇíÂÜçÁèæ„Åô„Çã„Å£„ÅΩ„ÅÑÔºü
 *
 * „Åü„Å®„Åà„Å∞
 *
 *   <my-component>
 *     <span>hoge</span>
 *   </my-component>
 *
 * „Åã„Çâ
 *
 *   <MyComponent children={[<span>hoge</span>]} />
 *
 * „Å´Áõ∏ÂΩì„Åô„Çã VDom „Çí‰Ωú„ÇãÔºü
 */
function toVdom (element, nodeName /* ÂÜçÂ∏∞„Åô„ÇãÊôÇ„ÅØ null „Å´„Å™„Çã„Å£„ÅΩ„ÅÑ */) {
  /* TextNode */
  if (element.nodeType === 3) {
    return element.data;
  }

  /* Ë¶ÅÁ¥†‰ª•Â§ñ„ÅÆË¨é„Éé„Éº„ÉâÔºà„Ç≥„É°„É≥„Éà„ÄÅDOCTYPE ÂÆ£Ë®Ä„ÄÅetcÔºâ */
  if (element.nodeType !== 1) {
    return null
  };

  /* „Åì„ÅÆ„Éé„Éº„Éâ„ÅÆ (slot ‰ª•Â§ñ„ÅÆ) attr „Çí props „Å´„Åæ„Å®„ÇÅ„Çã */
  let props = {};
  const a = element.attributes; /* Attr „ÅÆÈÖçÂàó„Å£„ÅΩ„ÅÑ‰Ωï„ÅãÔºàÂé≥ÂØÜ„Å´„ÅØ NamedMapÔºâ */
  for (let i = a.length; i--; ) {
    if (a[i].name !== 'slot') {
      props[a[i].name] = a[i].value;
      props[toCamelCase(a[i].name)] = a[i].value;
    }
  }

  /* Â≠êË¶ÅÁ¥†„Åü„Å°„Çí VDom Âåñ„Åô„Çã */
  let children = [];
  const cn = element.childNodes; /* Node „ÅÆÈÖçÂàó„Å£„ÅΩ„ÅÑ‰Ωï„ÅãÔºàÂé≥ÂØÜ„Å´„ÅØ NodeListÔºâ */
  for (let i = cn.length; i--; ) {
    const vnode = toVdom(cn[i], null);
    /* Â≠êË¶ÅÁ¥†„Åå slot attr „ÇíÊåÅ„Å£„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÄÅ children „Åß„ÅØ„Å™„ÅèË©≤ÂΩì„Åô„Çã prop „Å´Ê∏°„Åô
     * „Åü„Å®„Åà„Å∞ <span slot="message">Hello </span> „ÅØ chidlren „Åß„ÅØ„Å™„Åè message „Å´Ê∏°„Çã */
    const name = cn[i].slot;
    if (name) {
      props[name] = h(Slot, { name }, vnode);
    } else {
      children[i] = vnode;
    }
  }

  // Only wrap the topmost node with a slot
  const wrappedChildren = nodeName ? h(Slot, null, children) : children;
  return h(nodeName || element.nodeName.toLowerCase(), props, wrappedChildren);
}
